header {
    color: black;
    font-weight: bold;
    font-size: 2em;
    
    text-align: center;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
}

.row {
  background-color: darkgrey;
  margin-bottom: 0.5em;
}

.input-label {
  margin: 0.8em 0.3em;
  display: inline-block;
  width: 17%;
  text-align: right;
}

.input-field {
  width: 60%;
  height: 2em;
  border-radius: 3px;
  padding-left: 5px;
  font-size: 1em;
}

input[type="number"] {
  width: 3em;
}

button {
  width: 50%;

  margin: 0 auto;
  display: block;
  padding: 1em 0.3em 1em 0.3em;
  margin-bottom: 1em;
  margin-top: 0.8em;
  font-size: 1.1em;
  border-radius: 10px;
  
  background-color: black;
  color: white;
}

button:hover {
  background-color: white;
  color: black;
}

.radio-label {
  padding-top: 0.35em;
}

span.input-label {
  
  height: 4em;
}

.radio-container {
  display: -ms-flexbox;
  display: flex;
}

.radio-button-container {
  width: 50%;
  display: inline-block;
  padding: 0.3em 0em 0.3em 0em;
}

.radio-label {
  display: inline-block;
  width: 17%;
  text-align: right;
  margin: 0.3em;
}


