* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

 body {margin: 0;
        font-family: 'Roboto', sans-serif;
        font-size: 1.1em;
        background-color: #cccccc
      }

.container {
  padding-bottom: 1em;
  width: 33em;
  margin: 3em auto 0 auto;
  background-image: url('https://blog.malwarebytes.com/wp-content/uploads/2015/02/photodune-1124293-keys-s-900x506.jpg');
  border-radius: 10px;
  -webkit-box-shadow: 5px 5px #555555;
          box-shadow: 5px 5px #555555;
  
}

@media (max-width: 600px) { 
  .container {
    width: 100%;
    margin-top: 0;
    border-radius: 0;
    -webkit-box-shadow: 0px 0px #555555;
            box-shadow: 0px 0px #555555;
  }
}





